import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Dashboard from '@/components/Dashboard'
import NewPost from '@/pages/posts/New'
import PublishPost from '@/pages/posts/Publish'
import DraftPost from '@/pages/posts/Draft'
import SettingPost from '@/pages/posts/Setting'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/post/new',
      name: 'NewPost',
      component: NewPost
    },
    {
      path: '/post/publish',
      name: 'PublishPost',
      component: PublishPost
    },
    {
      path: '/post/draft',
      name: 'DraftPost',
      component: DraftPost
    },
    {
      path: '/post/setting',
      name: 'SettingPost',
      component: SettingPost
    }
  ]
})



