<template>
<div class="mt-4">
<div v-if="errors.length" class="alert alert-danger">
  <b>Please correct the following error(s):</b>
  <ul>
    <li v-for="error in errors">{{ error }}</li>
  </ul>
</div>
<form @submit.prevent="processForm">
<div class="row">
  <div class="col-lg-9">
    <div class="card no-border card-shadow">
      <h5 class="card-header" style="background-color: #f9f9f9!important;border-bottom: 0.5px dashed #ddd">{{ module }}</h5>
      <div class="card-body">
        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label>Post Title</label>
              <span class="text-danger">*</span>
              <input type="text" v-model="form.post_title" class="form-control form-sunda">
            </div>
        </div>
        <div class="col-md-12">
            <div class="form-group">
              <tinymce v-model="form.post_content"
               :plugins="myPlugins" :toolbar1="myToolbar1" :toolbar2="myToolbar2"
               :other="myOtherOptions">
              </tinymce>
            </div>
        </div>
      </div>
    </div>
    </div>
  </div>
  <div class="col-lg-3 mt-3">
    <div class="form-group">
      <label>Post Type</label>
      <span class="text-danger">*</span>
      <multiselect
        v-model="form.post_type"
        :options="post_type"
        placeholder="Select a Type"
        track-by="id"
        label="type"
        />
    </div>

    <div class="form-group">
      <label>Post Status</label>
      <span class="text-danger">*</span>
      <multiselect
        v-model="form.post_status"
        :options="post_status"
        placeholder="Select a Status"
        track-by="id"
        label="status"
        />
    </div>

    <div class="form-group">
      <label>Tags</label>
      <span class="text-danger">*</span>
      <multiselect
        v-model="form.post_tags"
        :options="options"
        :multiple="true"
        track-by="value"
        label="name"
        />
    </div>

    <div class="form-group">
      <label>Image</label>
      <span class="text-danger">*</span>
      <input type="file" class="form-control form-sunda" v-on:change="onFileChange">
    </div>

    <div class="row">
      <div class="col-lg-6">
          <div class="form-group">
            <button type="reset" class="btn btn-dark btn-block"><i class="fa fa-close"></i> Cancel</button>
          </div>
      </div>
        <div class="col-lg-6">
          <div class="form-group">
            <button type="submit" class="btn btn-primary btn-block"><i class="fa fa-check"></i> Submit</button>
          </div>
      </div>
    </div>
  </div>
</div>
</form>
</div>
</template>

<script>
import Editor from '@tinymce/tinymce-vue';
import Multiselect from 'vue-multiselect'
import axios from 'axios';

export default {
  components: {
    tinymce: Editor,
    multiselect: Multiselect
  },

  data () {
    return {
      selectedUser: {},
      module: 'Module Posts',
      errors: [],
      value: null,
      options: [
        {id:1, value: 'tags', name: 'Tags'},
        {id:2, value: 'tags2', name: 'Tags 2'}
      ],
      post_status: [
        {id:1, value: 'publish', status : 'Publish'},
        {id:2, value: 'draft', status : 'Draft'},
      ],
      post_type: [
        {id:1, value: 'posting', type : 'Posting'},
        {id:2, value: 'page', type : 'Page'},
      ],
      form: {
       post_title: '',
       post_content: '',
       post_type: '',
       post_status: '',
       post_tags: [],
       post_image: '',
      },
      /* Config can be declare here */
      myPlugins : [
          'advlist autolink lists link image charmap print preview anchor textcolor',
          'searchreplace visualblocks code fullscreen',
          'insertdatetime media table contextmenu paste code directionality template colorpicker textpattern'
      ],
      myToolbar1: 'undo redo | bold italic strikethrough | forecolor backcolor | template link | bullist numlist | ltr rtl | removeformat | image',
      myToolbar2 : '',
      myOtherOptions : {
          height: 200,
          templates: [
              {title: 'Test template 1', content: 'Test 1'},
              {title: 'Test template 2', content: 'Test 2'}
          ],
          content_css: 'css/tinymce-content.css'
          //,width:600,
          //directionality: 'rtl',
          //theme: 'modern',
          //menubar: false
          //, etc...
      }
    };
  },

  mounted(){
  },

  methods: {
    onFileChange(e) {
      this.form.post_image = e.target.files[0]
    },
   processForm: function(){
     var context = this;

     if (this.form.post_title && this.form.post_content) {
       var body = new FormData();

       body.set('post_title', this.form.post_title);
       body.set('post_content', this.form.post_content);
       body.set('post_type', this.form.post_type.value);
       body.set('post_status', this.form.post_status.value);
       body.set('post_image', this.form.post_image);

      this.errors = [];

       axios({
           method: 'post',
           url: API_URL + 'blog/save?token='+TOKEN_ACCESS,
           data: body,
           config: { headers: {'Content-Type': 'multipart/form-data' }}
           })
           .then(function (response) {
               if(response.data.type == 'error'){
                 loader.hide()
                 swal({
                   title: 'Error!',
                   text: 'Validation Error',
                   icon: 'error'
                 })
                 context.errors.push(response.data.message);
               } else {
                 loader.hide()
                 swal({
                   title: 'Success!',
                   text: 'Success Added',
                   icon: 'success'
                 })
                 context.$router.push({name : 'post'})
               }
           })
         .catch(function (response) {
             console.log(response);
         });

        return true;
      }
   }
  }
}
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



