<template>
  <div id="app">
    <Header :login="getLogin"/>
    <div id="main">
      <div class="container">
	       <div class="row">
	          <div class="col-md-12">
              <router-view></router-view>
            </div>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from './components/Header';
import HelloWorld from './components/HelloWorld';

import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'vue-loading-overlay/dist/vue-loading.min.css';

export default {
  name: 'App',
  components: {
    Header, HelloWorld
  },
  data () {
    return {
      pesan: 'Welcome to Your Vue.js Aspp',
      posts: [],
      getLogin: 'true'
    }
  },
  methods:{
    login: function(){
      if(this.getLogin == 'true'){
        this.getLogin = 'false';
      } else {
        this.getLogin = 'true';
      }
    }
  },
  computed: {
    isLogin: function(){
      if(this.getLogin == 'true'){
        this.getLogin = 'false';
      } else {
        this.getLogin = 'true';
      }
    }
  }
}
</script>

