     flex-direction: row;
     font-weight: 700;
   }

   .navbar-nav li{
     border-left: 0.2px solid #818fa5;
     padding: 10px;
   }

   .navbar-nav li a{
     color: #eee!important;
   }

   .nav-link {
     padding-right: .5rem !important;
     padding-left: .5rem !important;
   }

   /* Fixes dropdown menus placed on the right side */
   .ml-auto .dropdown-menu {
     left: auto !important;
     right: 0px;
   }

   .bg-blue{
     background-color: #0849b2!important;
   }

   .btn-primary{
     background-color: #0849b2!important;
   }

   .navbar-brand{
     color: #fff!important;
   }

   .dropdown-menu{
     background-color: #fff!important;
     border: none;
     box-shadow: 2px 3px 1px 1px #eee;
     border-top-right-radius: 0px;
     border-top-left-radius: 0px;
     border-bottom-right-radius: 10px;
     border-bottom-left-radius: 10px;
   }

   .dropdown-menu a.dropdown-item{
     color: #0849b2!important;
     font-weight: 700;
   }

   #top-navbar{
     border-bottom: 1px dashed #ccc;
     padding: 5px;
     background-color: #f8f8f8;
   }

 #footer {
   position: fixed;
   bottom: 0px;
   left: 0px;
   right: 0px;
   margin-bottom: 0px;
   padding: 10px;
 }

 .form-sunda{
   padding: 5px;
   border-radius: 0px;
   background-color: #f8f8f8;
   border: 2px solid #eee;
 }

 .form-sunda:focus{
   padding: 5px;
   border-radius: 0px;
   background-color: #f8f8f8;
   outline: none;
   box-shadow: none;
   border: 2px solid #0849b2;
   color: #0849b2;
 }
 .no-border {
   border: none!important;
 }

