   font-size: 20px;
 }
 @media only screen and (max-width: 1200px) {
   #top-navbar {
       display: none;
   }
 }
 #wrapper {
  padding-left: 0;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
