<template>
  <div class="hello">
    <div class="form-group">
      <label for="">{{this.$props.model}}</label>
      <input :type="type" class="form-control" :v-model="this.$props.model">
    </div>
  </div>
</template>

<script>

export default {
  name: 'Input',
  props: ['label', 'model', 'type'],
  data () {
    return {
      label: this.label,
      model: this.model,
      type: 'number'
    }
  }
}
</script>



