<template>
  <div class="hello">
  <loading
    :active.sync="isLoading"
    :is-full-page="true"
    />
  <div class="col-md-6 m-auto">
    <button type="button" v-on:click="ubah">Login</button>
    <form @submit.prevent="login">
      <h3 class="text-center mb-4">Login with your Credential</h3>
      <div class="form-group">
        <label for="">Username</label>
        <input type="text" class="form-control form-control-lg p-3 form-sunda" placeholder="Username / Email Addres" v-model="form.username">
      </div>
      <div class="form-group">
        <label for="">Password</label>
        <input type="password" class="form-control form-control-lg p-3 form-sunda" placeholder="Password" v-model="form.password">
      </div>
      <div class="form-group">
      <label for="">{{form.question}} = ?</label>
        <input type="number" class="form-control form-control-lg p-3 form-sunda" placeholder="Answer" v-model="form.hint">
      </div>
      <div class="form-group">
        <button type="submit" class="btn btn-primary btn-block btn-lg">Login</button>
      </div>
    </form>
  </div>
  </div>
</template>

<script>
import Loading from 'vue-loading-overlay';
import axios from 'axios';
import Input from './Input'

export default {
  name: 'HelloWorld',
  props: ['login'],
  components: {
    Loading
  },

  data () {
    return {
      msg: 'Welcome to Your Vue.js Aspp',
      isLoading: false,
      posts: [],
      test: '',
      isLogin: this.login,
      form: {
        username: '',
        password: '',
        hint: '',
        question: '',
        result: ''
      }
    }
  },

  mounted(){
    this.isLoading = false;
    this.getData();
    this.getHint();
  },

  methods: {
    ubah: function(event){
      if(this.isLogin == 'true'){
        this.isLogin = 'false';
      } else {
        this.isLogin = 'true';
      }
    },
    getData: function(){
      axios.get('http://localhost/dev/api/space/listing')
      .then(response => {
        this.posts = response.data.data;
      });
    },

    getHint: function(){
      const oneAsk = Math.floor(Math.random() * 100);
      const towAsk = Math.floor(Math.random() * 100);

      this.form.result = oneAsk + towAsk;
      this.form.question = oneAsk + ' + ' + towAsk;
    },

    login: function(){
      this.$session.set('jwt', 'oke')
      this.$router.push('/dashboard')
    }
  }
}
</script>



