<template>
  <div id="header-component">
    <!-- Sidebar -->
    <div class="nav-side-menu">
    <div class="brand bg-blue">Admin Sunda
    </div>
        <i class="fa fa-bars fa-2x toggle-btn text-light bg-blue" data-toggle="collapse" data-target="#menu-content"></i>
        <div class="menu-list" v-if="this.$props.login !== 'false'">
            <ul id="menu-content" class="menu-content collapse out">
                <li>
                    <router-link :to="{ name: 'Dashboard' }">
                        <i class="fa fa-dashboard fa-lg"></i> Dashboard
                    </router-link>
                </li>
                <li data-toggle="collapse" data-target="#products" class="collapsed">
                    <a href="#"><i class="fa fa-feed fa-lg"></i> Posts <span class="arrow"></span></a>
                </li>
                <ul class="sub-menu collapse" id="products">
                    <li><router-link :to="{ name: 'NewPost' }">New</router-link></li>
                    <li><router-link :to="{ name: 'PublishPost' }">Publish</router-link></li>
                    <li><router-link :to="{ name: 'DraftPost' }">Draft</router-link></li>
                    <li><router-link :to="{ name: 'SettingPost' }">Setting</router-link></li>
                </ul>
                <li data-toggle="collapse" data-target="#pages" class="collapsed">
                    <a href="#"><i class="fa fa-bookmark fa-lg"></i> Pages <span class="arrow"></span></a>
                </li>
                <ul class="sub-menu collapse" id="pages">
                  <li><a href="#">New</a></li>
                  <li><a href="#">Publish</a></li>
                  <li><a href="#">Draft</a></li>
                  <li><a href="#">Setting</a></li>
                </ul>
                <li data-toggle="collapse" data-target="#agenda" class="collapsed">
                    <a href="#"><i class="fa fa-calendar fa-lg"></i> Agenda <span class="arrow"></span></a>
                </li>
                <ul class="sub-menu collapse" id="agenda">
                  <li><a href="#">New</a></li>
                  <li><a href="#">Publish</a></li>
                  <li><a href="#">Draft</a></li>
                </ul>
                <li data-toggle="collapse" data-target="#gallery" class="collapsed">
                    <a href="#"><i class="fa fa-image fa-lg"></i> Gallery <span class="arrow"></span></a>
                </li>
                <ul class="sub-menu collapse" id="gallery">
                  <li><a href="#">New</a></li>
                  <li><a href="#">Publish</a></li>
                  <li><a href="#">Draft</a></li>
                  <li><a href="#">Album</a></li>
                  <li><a href="#">Setting</a></li>
                </ul>
                <li data-toggle="collapse" data-target="#category" class="collapsed">
                    <a href="#"><i class="fa fa-tag fa-lg"></i> Category <span class="arrow"></span></a>
                </li>
                <ul class="sub-menu collapse" id="category">
                  <li><a href="#">New</a></li>
                  <li><a href="#">Table</a></li>
                </ul>
                <li data-toggle="collapse" data-target="#access" class="collapsed">
                    <a href="#"><i class="fa fa-lock fa-lg"></i> Access <span class="arrow"></span></a>
                </li>
                <ul class="sub-menu collapse" id="access">
                  <li><a href="#">New Group</a></li>
                  <li><a href="#">New Group Access</a></li>
                </ul>
                <li data-toggle="collapse" data-target="#user" class="collapsed">
                    <a href="#"><i class="fa fa-user fa-lg"></i> User <span class="arrow"></span></a>
                </li>
                <ul class="sub-menu collapse" id="user">
                    <li><a href="#">New</a></li>
                    <li><a href="#">Registered</a></li>
                    <li><a href="#">Active</a></li>
                    <li><a href="#">Inactive</a></li>
                    <li><a href="#">Access Role</a></li>
                </ul>
                <li>
                    <a href="#">
                        <i class="fa fa-sign-out fa-lg"></i> Logout
                    </a>
                </li>
            </ul>
        </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'Header',
  props: ['login'],
  data () {
    return {
      pesan: 'Welcome to Your Vue.js Aspp',
      posts: [],
      isToggled: 'toggled',
      isLogin: this.login
    }
  },
  methods: {
    getToggle: function(event){
    //  this.$emit("update-is-toggled", 'false') ;
    this.isLogin = 'false'
    }
  }
}
</script>

<style>
.breadcrumb-sat{
	padding: 15px;
	border: 1px solid #ddd;
	background-color: #f0f0f0;
	border-radius: 0px!important;
}
.breadcrumb-sat a{
	color: #0c151c!important;
}
.nav-side-menu {
  overflow: auto;
  font-family: verdana;
  font-size: 12px;
  font-weight: 200;
  background-color: #fff;
  position: fixed;
  top: 0px;
  width: 250px;
  height: 100%;
  box-shadow: 1px 0px 3px 3px #ddd!important;
  color: #e1ffff;
}
.nav-side-menu .brand {
  background-color: #0c151c;
  line-height: 50px;
  display: block;
  text-align: center;
  font-size: 18px;
}
.nav-side-menu .toggle-btn {
  display: none;
}
.nav-side-menu ul,
.nav-side-menu li {
  list-style: none;
  padding: 0px;
  margin: 0px;
  line-height: 35px;
  cursor: pointer;
  /*
    .collapsed{
       .arrow:before{
                 font-family: FontAwesome;
                 content: "\f053";
                 display: inline-block;
                 padding-left:10px;
                 padding-right: 10px;
                 vertical-align: middle;
                 float:right;
            }
     }
*/
}
.nav-side-menu ul :not(collapsed) .arrow:before,
.nav-side-menu li :not(collapsed) .arrow:before {
  font-family: FontAwesome;
  content: "\f078";
  display: inline-block;
  padding-left: 10px;
  padding-right: 10px;
  vertical-align: middle;
  float: right;
}
.nav-side-menu ul .active,
.nav-side-menu li .active {
  border-left: 3px solid #d19b3d;
  background-color: #0c151c;
}

.nav-side-menu ul .active a,
.nav-side-menu li .active a{
  color: #333!important;
}

.nav-side-menu ul .sub-menu li.active,
.nav-side-menu li .sub-menu li.active {
  color: #333;
}
.nav-side-menu ul .sub-menu li.active a,
.nav-side-menu li .sub-menu li.active a {
  color: #333;
}
.nav-side-menu ul .sub-menu li,
.nav-side-menu li .sub-menu li {
  background-color: #f6f6f6;
  border: none;
  line-height: 28px;
  border-bottom: 1px solid #ddd;
  margin-left: 0px;
}
.nav-side-menu ul .sub-menu li:hover,
.nav-side-menu li .sub-menu li:hover {
  background-color: #fff;
}
.nav-side-menu ul .sub-menu li:before,
.nav-side-menu li .sub-menu li:before {
  font-family: FontAwesome;
  content: "\f105";
  display: inline-block;
  padding-left: 10px;
  padding-right: 10px;
  vertical-align: middle;
}
.nav-side-menu li {
  border-left: 3px solid #333;
  border-bottom: 1px dotted #ddd;
  padding: 10px;
}
.nav-side-menu li a {
  text-decoration: none;
  color: #162028;
  font-size: 16px;
  font-weight: 400;
  padding: 10px;
}
.nav-side-menu li a i {
  padding-left: 10px;
  width: 20px;
  padding-right: 20px;
}
.nav-side-menu li:hover {
  border-left: 3px solid #0849b2;
  background-color: #f0f0f0;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease;
  -ms-transition: all 1s ease;
  transition: all 1s ease;
}
@media (max-width: 767px) {
  .nav-side-menu {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
  }

  .nav-side-menu .toggle-btn {
    display: block;
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 10 !important;
    padding: 3px;
    background-color: #ffffff;
    color: #000;
    width: 40px;
    text-align: center;
  }
  .brand {
    text-align: left !important;
    font-size: 22px;
    padding-left: 20px;
    line-height: 50px !important;
  }
}
@media (min-width: 767px) {
  .nav-side-menu .menu-list .menu-content {
    display: block;
  }
  #main {
    width:calc(100% - 250px);
    float: right;
  }
  .nav-side-menu  .brand {
    padding: 20px;
  }
}
</style>



