<template>
  <div class="hello">
  <loading
    :active.sync="isLoading"
    :is-full-page="true"
    :can-cancel="true"
    />
  <div class="card no-border card-shadow scrollable mt-4">

    <div class="card-header" style="background-color: #f9f9f9!important;border-bottom: 0.5px dashed #ddd">
      <a class="btn header-text">Publish Post</a>

      <router-link :to="{ path: 'agendaAdd'}" class="btn btn-primary pull-right">
        <i class="fa fa-plus"></i> Add Post
      </router-link >
    </div>

    <div class="row mt-3 mb-3 text-center">

      <div class="col-md-4">
        <i class="fa fa-5x fa-eye"></i>
        <span> <strong>4.500</strong> </span>
      </div>

      <div class="col-md-4">
        <i class="fa fa-5x fa-list-alt"></i>
        <span> <strong>50 Posts</strong> </span>
      </div>

      <div class="col-md-4">
        <i class="fa fa-5x fa-trash"></i>
        <span> <strong>50 Draft</strong> </span>
      </div>
    </div>

    <div class="dropdown-divider"></div>
    <div class="card-body">
      <div class="row">

        <div class="col-md-6">
          <div class="form-group">
            <input type="text" class="form-control form-sunda" placeholder="Keyword">
          </div>
        </div>

        <div class="col-md-2">
          <div class="form-group">
            <select class="form-control form-sunda">
              <option value="1">Actived</option>
              <option value="2">Deactived</option>
            </select>
          </div>
        </div>

        <div class="col-md-2">
          <div class="form-group">
            <select class="form-control form-sunda">
              <option value="asc">Ascending</option>
              <option value="desc">Descending</option>
            </select>
          </div>
        </div>

        <div class="col-md-2">
          <div class="form-group">
            <select class="form-control form-sunda">
              <option>Per Page</option>
              <option value="5">5</option>
              <option value="10">10</option>
              <option value="20">20</option>
              <option value="50">50</option>
              <option value="100">100</option>
            </select>
          </div>
        </div>

      </div>

      <div class="dropdown-divider"></div>
      <div>
        <h5>1</h5>
        <p>
          <span>Edit</span>
          <span>Show</span>
          <span>Delete</span>
        </p>
        <hr/>
      </div>
      <button class="btn btn-primary btn-block mt-3 mb-5">More</button>
    </div>
  </div>
  </div>
</template>

<script>
import Loading from 'vue-loading-overlay';
import axios from 'axios';

export default {
  name: 'HelloWorld',
  components: {
    Loading
  },

  data () {
    return {
      msg: 'Welcome to Your Vue.js Aspp',
      isLoading: false,
      posts: [],
      form: {
        username: '',
        password: '',
        hint: '',
        question: '',
        result: ''
      }
    }
  },

  mounted(){
    this.getData();
    this.getHint();
  },

  methods: {
    getData: function(){
      this.isLoading = true;
      axios.get('http://localhost/dev/api/space/listing')
      .then(response => {
        this.isLoading = false
        this.posts = response.data.data;
      })
      .catch(response => {
        this.isLoading = false
      });
    },

    getHint: function(){
      const oneAsk = Math.floor(Math.random() * 100);
      const towAsk = Math.floor(Math.random() * 100);

      this.form.result = oneAsk + towAsk;
      this.form.question = oneAsk + ' + ' + towAsk;
    },

    login: function(){
      this.$session.set('jwt', 'oke')
      this.$router.push('/')
    }
  }
}
</script>



